.preloader {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 1;
  margin-top: 0;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: hidden !important;
  right: 0;
  z-index: 999999;
  text-align: center;
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #2196F3;
  width: 120px;
  margin: 200px auto;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  z-index: 100;
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);
  }
}
.fadeInUp {
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(40px);
    -ms-transform: translateX(40px);
    transform: translateX(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
}
.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}

.dropdown {
  -ms-transform: translateY(-20px) scaleY(0.9);
  -webkit-transform: translateY(-20px) scaleY(0.9);
  transform: translateY(-20px) scaleY(0.9);
  -webkit-transition: transform cubic-bezier(0.35, 1.65, 0.65, 1.1) 250ms, opacity ease 250ms;
  transition: transform cubic-bezier(0.35, 1.65, 0.65, 1.1) 250ms, opacity ease 250ms;
  position: absolute;
  top: 100%;
  left: -9999px;
  z-index: 100025;
  max-width: none;
  min-width: initial;
  padding: 5px;
  margin: 0;
  color: #666;
  text-align: left;
  text-transform: none;
  list-style: none;
  background: #fff;
  border-radius: 3px;
  opacity: 0;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3), 0 3px 10px rgba(0, 0, 0, 0.5);
}
.dropdown:after, .dropdown:before {
  position: absolute;
  display: block;
  content: "";
  border: solid 6px transparent;
  border-top: none !important;
}
.dropdown:before {
  top: -7px;
  left: 14px;
  border-bottom-color: rgba(0, 0, 0, 0.15);
  border-width: 7px;
}
.dropdown:after {
  top: -6px;
  left: 15px;
  border-bottom-color: #fff;
}
.dropdown li {
  font-size: 1.3rem;
  position: relative;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  line-height: 25px;
  color: inherit;
  vertical-align: inherit;
  list-style: inherit;
}
.dropdown li a {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: #333;
  border-radius: 3px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box;
  font-size: 13px;
  font-weight: bold;
  padding: 5px 10px;
  text-align: left;
  display: block;
}
.dropdown li a:hover {
  background-color: #ebebeb;
  color: #cc0000;
}

.open > .dropdown {
  -ms-transform: translateY(0) scaleY(1);
  -webkit-transform: translateY(0) scaleY(1);
  transform: translateY(0) scaleY(1);
  top: 100%;
  left: 0;
  opacity: 1;
}
.open > .dropdown-center {
  left: 50%;
  margin-left: -150px;
}
.open > .dropdown-center:after, .open > .dropdown-center:before {
  left: 50%;
  margin-left: -6px;
}
.open > .dropdown-center.dropdown-wide {
  margin-left: -150px;
}
.open > .dropdown-right {
  right: 0;
  left: auto;
}
.open > .dropdown-right:after, .open > .dropdown-right:before {
  left: auto;
  right: 16px;
}

.dropdown--notifications {
  padding: 0;
}

.dropdown-wide {
  min-width: 300px;
}

